
import React from "react";

// SubViews
import Cell from "./SubViewComponentsJogo/tabuleiro/Cell"



export default class tabuleiro extends React.Component {

    constructor(props) {
        
        super(props);
        //console.log(props);
    }
    

    // Linked Functions



    // End Linked Funcitons


    // View Composition Functions

    renderCell(lin,col) {
        return <Cell 
            ref={React.createRef()}
            key={col+lin*this.props.ControleJogo.ControleModo.getAltura()}
            index={col+lin*this.props.ControleJogo.ControleModo.getAltura()}
            ControleJogo={this.props.ControleJogo}
            update={this.props.update}
        />
    }

    renderRow(lin,col){
        var cellBoard = [];
        for (let index = 0; index < col; index++) {
            cellBoard.push(this.renderCell(lin,index));
        }
        return React.createElement('div', { className: "d-flex" , key: lin}, cellBoard);
    }


    renderBoard(lin,col) {
        var rowsBoard = [];
        for (let index = 0; index < lin; index++) {
            rowsBoard.push(this.renderRow(index,col));
        }
        return React.createElement('div', { className: "main-board container pt-6 px-md-5" }, rowsBoard);
    }

    // End View Composition Functions
   

    render() {
       
        return (
            <div className="flex-grow-1 w-100 tabuleiro bg-color3 arround-tl-12">
                {/* <h3>tabuleiro</h3>
                <div>
                    statusWords: {this.props.ControleJogo.state.focus}
                </div> */}

                {this.renderBoard(this.props.ControleJogo.ControleModo.getAltura(),this.props.ControleJogo.ControleModo.getLargura())}
            </div>
        );
    }
}